"use client";

import React, { useState, useEffect } from "react";

// Components
import Header from "@/components/layout/Header";
import Footer from "@/components/layout/Footer";
import HeroSection from "@/components/sections/HeroSection";
import StatsSection from "@/components/sections/StatsSection";
import ServicesSection from "@/components/sections/ServicesSection";
import EventsSection from "@/components/sections/EventsSection";
import PastEventsCarousel from "@/components/sections/PastEventsCarousel";
import AboutSection from "@/components/sections/AboutSection";
import ContactSection from "@/components/sections/ContactSection";
import FoundingMembersSection from "@/components/sections/FoundingMembersSection";
import SiteWrapper from "@/components/layout/SiteWrapper";

// Hook personnalis√© pour la navigation
import useScrollSpy from "@/hooks/useScrollSpy";

export default function Home() {
  const [scrollY, setScrollY] = useState(0);
  const { activeSection, scrollToSection } = useScrollSpy();

  useEffect(() => {
    const handleScroll = () => setScrollY(window.scrollY);
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <SiteWrapper>
      <div className="min-h-screen bg-white dark:bg-gray-900 text-gray-900 dark:text-gray-100">
        <Header
          scrollY={scrollY}
          activeSection={activeSection}
          scrollToSection={scrollToSection}
        />

        <main>
          {/* Chaque section avec largeur limit√©e */}
          <HeroSection scrollToSection={scrollToSection} />
          <StatsSection />
          <SiteWrapper maxWidth="max-w-5xl">
            <ServicesSection />
          </SiteWrapper>
          <EventsSection />
          <PastEventsCarousel />
          <SiteWrapper maxWidth="max-w-6xl">
            <FoundingMembersSection />
          </SiteWrapper>
          <AboutSection />
          <ContactSection />
        </main>

        <Footer />
      </div>
    </SiteWrapper>
  );
}
